import React from 'react'
import Mytab from './Mytab';
import {connect} from 'react-redux'

const Star = (props) => {
  console.log(props.mystar);
  return (
    <div>
      {
        props.mystar.map((item,idx)=>
          <li key={idx}>
            {item}
            <button
              onClick={()=>
                props.dispatch({
                  type:'DEL_STAR',
                  index: idx
                })
              }>删除</button>
          </li>)
        }
      <Mytab/>
    </div>
  )
}

const mapStateToProps = (state) => {
  return {
    mystar:state.star
  }
}

export default connect(mapStateToProps)(Star)