import React,{useState} from 'react'
import { ListView } from 'antd-mobile';
import {BrowserRouter as  Router, Route, Redirect, NavLink, withRouter } from 'react-router-dom'
import {getgoodslist} from './actionCreators';
import {connect} from 'react-redux'

const NUM_ROWS = 10;
let pageIndex = 0;

function genData(pIndex = 0) {
  const dataBlob = {};
  for (let i = 0; i < NUM_ROWS; i++) {
    const ii = (pIndex * NUM_ROWS) + i;
    dataBlob[`${ii}`] = `row - ${ii}`;
  }
  return dataBlob;
}

class Things extends React.Component {
  constructor(props) {
    super(props);
    const dataSource = new ListView.DataSource({
      rowHasChanged: (row1, row2) => row1 !== row2,
    });

    this.state = {
      dataSource,
      isLoading: true,
    };
  }

  componentDidMount() {
    setTimeout(() => {
      this.rData = genData();
      this.setState({
        dataSource: this.state.dataSource.cloneWithRows(this.rData),
        isLoading: false,
      });
    }, 600);
  }

  onEndReached = (event) => {
    if (this.state.isLoading && !this.state.hasMore) {
      return;
    }
    this.setState({ isLoading: true });
    setTimeout(() => {
      this.rData = { ...this.rData, ...genData(++pageIndex) };
      this.setState({
        dataSource: this.state.dataSource.cloneWithRows(this.rData),
        isLoading: false,
      });
    }, 1000);
  }

  render() {
    function getdata(props){props.dispatch(getgoodslist())};
    getdata(this.props);
    if(this.props.mydata == undefined){
      getdata(this.props);
      return (
        <div style={{ padding: 50, textAlign: 'center' }}>
          {'Loading...'}
        </div>
      )
    }else{
      //console.log(this.props.mydata);
      const data = this.props.mydata
      const separator = (sectionID, rowID) => (
        <div
          key={`${sectionID}-${rowID}`}
          style={{
            backgroundColor: '#ECECEC',
            height: 8,
            borderTop: '1px solid #ECECEC',
            borderBottom: '1px solid #ECECEC',
          }}>
        </div>
      );
      let index = data.length - 1;
      const row = () => {
        if (index < 0) {
          index = data.length - 1;
        }
        const obj = data[index--];
        return (
          <div 
            key={obj.id} 
            style={{backgroundColor:'#ECECEC'}}
          >
            <div style={{ 
              display: "inline-block", 
              //marginTop:'10px',
              marginBottom:'10px',
              marginLeft: '10px', 
              borderRadius:'8px',
              backgroundColor:'white', 
              float:'left'
            }}
            onClick={() => {
              //console.log(this.props);
              this.props.history.push('/shop',{src:obj.img,goods:obj.title})
            }}>
              <img 
                style={{width: '170px', height: '170px'}} 
                src={obj.img} 
                alt="" />
              <div style={{
                width: '170px', 
                position:'relative', 
                lineHeight: 1.5
              }}>
                <div style={{
                  height:'32px', 
                  marginBottom: '8px', 
                  color:'black', 
                  fontSize:'16px', 
                  overflow:"hidden"
                }}>
                  {obj.title}
                </div>
                <div>
                  <span style={{ fontSize: '20px', color: '#FF6E27' }}>
                    {'￥ '+ obj.price}
                  </span>
                </div>
              </div>
            </div>
          </div>
        );
      };
      return (
        <ListView
          ref={el => this.lv = el}
          dataSource={this.state.dataSource}
          renderHeader={() => 
            <span style={{
              width:'100px',
              color: 'orangered', 
              textAlign: 'center', 
              padding:'100px'
            }}>
              →❤你可能还喜欢←
            </span>}
          renderFooter={() => (
            <div style={{ padding: 50, textAlign: 'center' }}>
              {this.state.isLoading ? 'Loading...' : 'Loaded'}
            </div>
          )}
          renderRow={row}
          renderSeparator={separator}
          className="am-list"
          useBodyScroll
          scrollRenderAheadDistance={500}
          onEndReached={this.onEndReached}
          onEndReachedThreshold={5}
          quickSearchBarTop
          horizontal={true}
        />
      );
    }
  }
}

const mapStateToProps = (state) => ({
  mydata:state.things.datalist
})

export default connect(mapStateToProps)(withRouter(Things))