import React from 'react'
import {connect} from 'react-redux'
import {login} from './actionCreators'

const Login = (props) => {
  const log = () => {
    props.dispatch(login());
    if(props.userinfo == undefined){
      props.dispatch(login());
    }
    if(props.userinfo !== undefined){
      localStorage.setItem('userinfo',props.userinfo)
      props.history.replace(props.location.state);
    }
  }
  return (
    <div>
      <div style={{width:'60%',height:'500px',margin:'auto',marginTop:'30%'}}>
        账号：
        <input type='text'/>
        <br/>
        <br/>
        密码：
        <input type='password'/>
        <br/>
        <br/>
        <button onClick={log}>登录</button>
        <button>注册</button>
      </div>
    </div>
  )
}

const mapStateToProps = (state) => ({
  userinfo:state.info.userinfo
})

export default connect(mapStateToProps)(Login)