import React from 'react'
import {BrowserRouter as Link, NavLink, Redirect } from 'react-router-dom'
import './firstHeader.css'

const FirstHeader = () => {
  return (
    <div className='searchTab' style={{
      display:"flex",
      width:'100%',
      height:'39px',
      top:0,
      backgroundColor:'rgb(255,133,42)',
      position:"relative",
      justifyContent:'space-between'
    }}>
      <span>
        <i className='iconfont icon-taobao' style={{
          fontSize:28,
          left:'8px',
          top:'2px',
          color:'rgb(255,133,42)',
          backgroundColor:'white',
          position:"absolute",
          margin:'0 auto'
        }}/>
      </span>
      <span style={{
        lineHeight:'23px',
        height:'23px',
        width:'85%',
        margin:'8px 10px',
        color:'white',
        backgroundColor:'orangeRed',
        textAlign:'center',
        borderRadius:'8px'
      }}>
        <NavLink to='/search' style={{color:'white'}}>
          <i className='iconfont icon-soushuo'/>
          寻找宝贝店铺
        </NavLink>
      </span>
    </div>
  )
}

export default FirstHeader