import React from 'react'
import { Carousel, WingBlank } from 'antd-mobile';

class Zoumadeng extends React.Component {
  state = {
    data: ['1', '2', '3','4','5','6','7','8'],
    src: ['https://gw.alicdn.com/imgextra/i3/191/O1CN01VWfpT01DHWHQurTZ7_!!191-0-lubanu.jpg',
          'https://aecpm.alicdn.com/simba/img/TB1CWf9KpXXXXbuXpXXSutbFXXX.jpg_q50.jpg',
          'https://aecpm.alicdn.com/simba/img/TB15tIjGVXXXXcoapXXSutbFXXX.jpg',
          'https://gw.alicdn.com/imgextra/i4/128/O1CN01L9c1rK1CofKGrrN4Z_!!128-0-ppp-picassogw.jpg',
          'https://gw.alicdn.com/imgextra/i1/2/O1CN0169xZ5Y1BsxQEkuxfV_!!2-0-lubanu.jpg',
          'https://gw.alicdn.com/imgextra/i4/81/O1CN01LbIJTv1CT8iDY67wG_!!81-0-lubanu.jpg',
          'https://img.alicdn.com/tps/i4/https://img.alicdn.com/imgextra/i4/6000000003172/O1CN01ZWE1x71ZIowtKsQod_!!6000000003172-0-octopus.jpg',
          'https://gw.alicdn.com/imgextra/i3/86/O1CN01QaJCKY1CVQgwb9GvC_!!86-0-lubanu.jpg'],
    imgHeight: '500px',
    imgWidth: '100%',
  }
  componentDidMount() {
    setTimeout(() => {
      this.setState({
        data: ['1', '2', '3','4','5','6','7','8'],
      });
    }, 100);
  }
  render() {
    return (
      <WingBlank style={{marginLeft:'0px', marginRight:'0px'}}>
        <Carousel
          autoplay={true}
          infinite={true}
          dotActiveStyle={{backgroundColor:'orangered'}}
          autoplayInterval='3000'
        >
          {this.state.data.map(val => (
            <a
              key={val}
              href="#"
              style={{ 
                display:'inline-block',
                width: this.state.imgWidth,  
                height: this.state.imgHeight 
              }}
            >
              <img
                src={this.state.src[val-1]}
                alt=""
                style={{ width: '100%', verticalAlign: 'top' ,height:'130px'}}
                onLoad={() => {
                  window.dispatchEvent(new Event('resize'));
                  this.setState({ imgHeight: 'auto' });
                }}
              />
            </a>
          ))}
        </Carousel>
      </WingBlank>
    );
  }
}

export default Zoumadeng