import React from 'react'
import { NavLink } from 'react-router-dom'
import BuyTab from './BuyTab'

const Shop = (props) => {
  let img = props.location.state.src;
  return (
    <div>
      {/* 顶部小盒子 */}
      <div style={{position:"fixed"}}>
        {/* 返回键 */}
        <button style={{
          left:'20px',
          margin: 20, 
          padding:'5px 5px', 
          border: '1px solid transparent',
          backgroundColor:'transparent'
        }}>
          <NavLink to='/' style={{color:'black'}}>
            <span 
              className='iconfont icon-fanhui' 
              style={{fontSize:'30px'}}>
            </span>
          </NavLink>
        </button>
        {/* 购物车 */}
        <button style={{
          position:'absolute',
          margin:'28px 235px', 
          padding:'5px 5px',
          float:'right', 
          border: '1px solid transparent',
          outline: 'none', 
          borderRadius:'50%',
          backgroundColor:'black'
        }}>
          <NavLink to='/cart' style={{color:'black'}}>
            <span 
              className='iconfont icon-gouwuche' 
              style={{fontSize:'18px',color:'#EBEBEB'}}>
            </span>
          </NavLink>
        </button>
      </div>
      {/* 图片详情 */}
      <div>
        <img 
          src= {img}
          style={{width:'100%', height:'400px'}}/>
      </div>
      {/* 底部小盒子 */}
      <BuyTab/>
    </div>
  )
}

export default Shop