import React from 'react';
import { Button, TabBar } from 'antd-mobile';
import { withRouter } from 'react-router-dom';

class Mytab extends React.Component {
  render() {
    const { pathname } = this.props.location;
    return (
      <div 
        style={ {
          position: 'fixed',
          width: '100%',
          bottom: 0
      }}>
        <TabBar
          unselectedTintColor="#949494"
          tintColor="rgb(255,133,42)"
          barTintColor="white"
        >
          <TabBar.Item
            title="首页"
            key="home"
            icon={
              <i className='iconfont icon-Homehomepagemenu'></i>                           
            }
            selectedIcon={
              <i className='iconfont icon-Homehomepagemenu1'></i>                           
            }
            selected={ pathname === '/' }
            onPress={ () => {
              this.props.history.push('/');
            }}
            data-seed="logId">
          </TabBar.Item>
          <TabBar.Item
            icon={
              <i className='iconfont icon-shoucang'></i>                           
            }
            selectedIcon={
              <i className='iconfont icon-shoucang1'></i>                           
            }
            title="收藏"
            key="star"
            selected={ pathname === '/star' }
            onPress={ () => {
              if(!localStorage.getItem('userinfo')){
                this.props.history.push({pathname:'/login',state:this.props.location});
              }else{
                this.props.history.push('/star')
              }
            } }
            data-seed="logId1">
          </TabBar.Item>
          <TabBar.Item
            icon={
              <i className='iconfont icon-weibiaoti'></i>
            }
            selectedIcon={
              <i className='iconfont icon-dingdanjianying'></i>
            }
            title="订单列表"
            key="List"
            selected={ pathname === '/list' }
            onPress={ () => {
              this.props.history.push('/list')
            }}>
          </TabBar.Item>
          <TabBar.Item
            icon={
              <i className='iconfont icon-wode'></i> 
            }
            selectedIcon={
              <i className='iconfont icon-wode1'></i>
            }
            title="我的淘宝"
            key="my"
            selected={ pathname === '/my' }
            onPress={ () => {
              this.props.history.push('/my')
            }}>
          </TabBar.Item>
        </TabBar>
      </div>
    );
  }
}

export default withRouter(Mytab);